<template>
    <div class="top-left-style">
        <div class="main-style" v-loading="topLoading" element-loading-background="rgba(0, 0, 0, 0.5)"
             element-loading-text="加载中">
            <div class="left-title">
                停电信息发布监控
            </div>
            <div class="left-details">
                <div class="details-inline" v-for="(item,i) in tdMap " :key="i">
                    <div class="inline-title">{{item.name}}</div>
                    <div class="inline-main">
                        <div class="main-info" v-for="(v,k,z) in item.data" :key="z">
                            <div class="keyInfo">{{k}}</div>
                            <div class="valueInfo">{{v}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
<!--        <div class="main-style">-->
<!--            <div class="left-title">-->
<!--                临时播报-->
<!--            </div>-->
<!--            <div class="left-details">-->

<!--            </div>-->
<!--        </div>-->
    </div>
</template>

<script>
    import Interface from '../../../base/mixin/remote-work-monitoring';
    import {dataChange, translateFilter} from '../../../commonJS/dataChanges'
    import qs from 'qs'

    export default {
        name: "topLeftComponent",
        mixins: [Interface],
        data() {
            return {
                translateMap: {
                    '01': '计划停电',
                    '02': '故障停电',
                    // '02': '电网故障停限电',
                    '03': '违约停电',
                    '04': '窃电停电',
                    '05': '欠费停电',
                    '06': '有序用电停电',
                    '07': '临时停电',
                    '99': '其他停电',
                },
                statusMap: {
                    '01': '全部送电', '02': '未执行', '03': '未送电', '04': '正执行'
                    // '01': '已送电', '02': '未执行', '03': '取消计划', '04': '正执行'
                },
                tdMap: [],
                topLoading:true
            }
        },
        watch: {},
        created() {
            this.getBlackoutReleaseMonitoring(qs.stringify({
                timeStr: this.$parent.dateParams
            })).then(res => {
                this.topLoading=false;
                this.dataFilter(res);
            }).catch(()=>{
                this.topLoading=false;
            })
        },
        mounted() {
        },
        methods: {
            dataFilter(res) {
                let data = translateFilter(res, this.translateMap, 'typecode', 'typeName');
                data = translateFilter(data, this.statusMap, 'status', 'statusName')
                let _info = dataChange(data, 'typecode', 'typeName');
                let tdMap = [], includesCodes = [];
                let codes = ['01', '07', '02','99'], status = ['03', '01'];
                if (_info) {
                    //其他放到数组最后
                    let other = {}
                    _info.forEach((item,index)=>{
                        if (item.name=='99'){  //其他
                            other =item;
                            _info.splice(index,1)
                            return;
                        }
                    })
                    _info.push(other)
                    //其他放到数组最后
                    for (let item of _info) {
                        if (codes.includes(item.name)) {
                            let _obj = {}, endObjs = {}
                            if (item.data) {
                                for (let sys of item.data) {
                                    _obj[sys.status] = sys.ccsum + '';
                                }
                            }
                            for (let value of status) {
                                if (!endObjs[this.statusMap[value]]) {
                                    endObjs[this.statusMap[value]] = _obj[value] ? _obj[value] : '0'
                                }

                            }
                            tdMap.push({name: item.typeName, typecode: item.name, data: endObjs});
                            includesCodes.push(item.name);
                        }
                    }
                    // 无数据补充数据
                    if (includesCodes.length !== codes.length) {
                        for (let v of codes) {
                            if (!includesCodes.includes(v)) {
                                let _objs = {
                                    name: this.translateMap[v], typecode: v
                                };
                                let _datas = {}
                                for (let v of status) {
                                    _datas[this.statusMap[v]] = 0
                                }
                                _objs.data = _datas;
                                this.tdMap.push(_objs)
                            } else {
                                for (let item of tdMap) {
                                    if (item.typecode === v) {
                                        this.tdMap.push(item)
                                    }
                                }
                            }
                        }
                    }else{
                        this.tdMap = tdMap
                    }
                }else{
                    for(let v of codes){
                        let _objs = {
                            name: this.translateMap[v], typecode: v
                        };
                        let _datas = {}
                        for (let v of status) {
                            _datas[this.statusMap[v]] = 0
                        }
                        _objs.data = _datas;
                        this.tdMap.push(_objs)
                    }
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .top-left-style {
        width: 100%;
        height: 100%;
        position: relative;

        .main-style {
            width: 100%;
            height: calc(100% - 15px);
            position: relative;
            border: 1px solid #1c6886;
            border-radius: 5px;

            .left-title {
                width: 30px;
                height: calc(100%);
                float: left;
                align-items: center;
                justify-items: center;
                display: flex;
                box-shadow: #21a8ae 0px 0px 10px inset;
                background: linear-gradient(180deg, rgba(3, 209, 218, .3), #183466);
                color: #fff;
                text-align: center;
                font-size: 20px;
                line-height: 28px;
                border-radius: 5px 0px 0px 5px;

                .title-main {
                    background: linear-gradient(180deg, rgba(15, 146, 229, .3), #061b3a);
                    color: #fff;
                }
            }

            .left-details {
                width: calc(100% - 42px);
                padding-left: 10px;
                height: calc(100% - 10px);
                float: left;
                position: relative;
                padding-bottom: 10px;

                .details-inline {
                    height: calc(25% - 10px);
                    width: calc(100% - 10px);
                    position: relative;
                    background: #19317c;
                    margin-top: 10px;

                    .inline-title {
                        width: 40px;
                        float: left;
                        color: #fff;
                        font-size: 20px;
                        padding: 8px 10px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        height: calc(100% - 20px);
                        line-height: 26px;
                    }

                    .inline-main {
                        width: calc(100% - 60px);
                        float: left;
                        font-size: 17px;
                        position: relative;
                        height: 100%;
                        display: flex;
                        justify-items: center;
                        align-items: center;

                        .main-info {
                            margin-right: 6px;
                            height: calc(100% - 12px);
                            float: left;
                            border-radius: 5px;
                            text-align: center;
                            max-height: 50px;
                            position: relative;

                            .keyInfo {
                                font-weight: 700;
                                line-height: 20px;
                            }

                            .valueInfo {
                                font-weight: 700;
                                font-size: 16px;
                                height: calc(100% - 20px);
                                width: 100%;
                                display: flex;
                                justify-content: center;
                                align-items: center;

                            }
                        }

                        .main-info:nth-child(1) {
                            color: #000000;
                            width: calc(50% - 6px);
                            background: linear-gradient(to bottom, #00e9f6, #15529e);
                        }

                        .main-info:nth-child(2) {
                            color: #000000;
                            width: calc(50% - 6px);
                            background: linear-gradient(to bottom, orange, #df896d);
                        }

                        .main-info:nth-child(3) {
                            color: #fff;
                            width: calc(50% - 6px);
                            background: linear-gradient(to bottom, #00d1a2, #00b18a);
                        }
                    }
                }
            }

        }

        .main-style:last-child {
            margin-top: 10px;
        }
    }
</style>